import axios from "axios";

const ADD_ARRAY_TO_SLIDES = 'ADD_ARRAY_TO_SLIDES';
const INIT_SLIDER = 'INIT_SLIDER';
const ADD_SELL_LEADERS = 'ADD_SELL_LEADERS';
const INIT_SELL_LEADERS = 'INIT_SELL_LEADERS';
const CLEAN_SELL_LEADERS = 'CLEAN_SELL_LEADERS';
const SET_DATA_TO_CATALOG = 'SET_DATA_TO_CATALOG';
const CHANGE_MIN_AND_MAX_CATALOG = 'CHANGE_MIN_AND_MAX_CATALOG';
const INIT_CATALOG = 'INIT_CATALOG';


export const changeMinAndMaxCatalog = (min, max) => {
    return {
        type: CHANGE_MIN_AND_MAX_CATALOG,
        min,
        max
    }
}

export const setDataToCatalog = (params) => {
    return {
        type: SET_DATA_TO_CATALOG,
        params
    }
}

export const addLeaders = (objItem) => {
    return {
        type: ADD_SELL_LEADERS,
        objItem
    }
}

export const addArrayToSlides = (array) => {
    return {
        type: ADD_ARRAY_TO_SLIDES,
        array
    }
}

export const setInitCatalog = (init) => {
    return {
        type: INIT_CATALOG,
        init
    }
}

export const initSliderAC = (init) => {
    return {
        type: INIT_SLIDER,
        init
    }
}
export const initSellLeadersAC = (init) => {
    return {
        type: INIT_SELL_LEADERS,
        init
    }
}
export const cleanSL = () => {
    return {
        type: CLEAN_SELL_LEADERS,
    }
}

let initialState = {
    minChangingPriceCatalog: 0,
    maxChangingPriceCatalog: 0,
    slidesResult: [],
    catalogData: [],
    initSlider: false,
    initSellLeaders: false,
    initCatalog: false,
    slides: [
        ["3407839", "2014851", "2368166", "2202103", "2364521"],
        ["2006123", "1913494", "2361998", "2330523", "2215878"],
        ["2200596", "2315906", "2241285", "2344872", "2305801"]

 ],
    listCategories: [
        {minecraft: "124863"},
        {steam: "124864"},
        {origin: "124865"},
        {uplay: "124866"},
        {xbox: "124867"},
        {software: "124868"}
    ],
    sellLeaders: []

}

const ProductsReducer = (state = initialState, action) => {

    switch (action.type) {

        case INIT_CATALOG: return {
            ...state,
            initCatalog: action.init
        }

        case CHANGE_MIN_AND_MAX_CATALOG: return {
            ...state,
            minChangingPriceCatalog: action.min,
            maxChangingPriceCatalog: action.max
        }

        case ADD_SELL_LEADERS: return {
            ...state,
            sellLeaders: action.objItem
        }

        case SET_DATA_TO_CATALOG: return {
            ...state,
            catalogData: action.params
        }

        case ADD_ARRAY_TO_SLIDES: return {
            ...state,
            slidesResult: action.array
        }
        case INIT_SLIDER: return {
            ...state,
            initSlider: action.init
        }
        case INIT_SELL_LEADERS: return {
            ...state,
            initSellLeaders: action.init
        }
        case CLEAN_SELL_LEADERS: return {
            ...state,
            sellLeaders: []
        }

        default: return state;
    }
}


export const requestItemsFromCategories = (category = false, params = false) => async (dispatch) => {
    let categories = initialState.listCategories;
    let arrForIdItems = [];

    if (!!params) {
        categories = categories.filter(param => params.indexOf(Object.keys(param)[0]) !== -1)
    }
    console.log(categories)
    const setDataState = (sortArray) => {
        let resultArray = [];
        console.log("sort arr " + sortArray.length)
        for (let s = 0; s < sortArray.length; s++) {
            if (s === 60 && !!category) break
            resultArray.push({
                id: sortArray[s].id,
                name: sortArray[s].name,
                cnt_sell: sortArray[s].cnt_sell === -1 ? "Скрыто" : sortArray[s].cnt_sell,
                price: sortArray[s].price_rub,
                imgItem: `https://graph.digiseller.ru/img.ashx?id_d=${sortArray[s].id}&w=230&h=160`
            })
        }
        if (!!category) {
            dispatch(addLeaders(resultArray));
            dispatch(initSellLeadersAC(true))
        } else {
            dispatch(setDataToCatalog(resultArray))
            dispatch(setInitCatalog(true))
        }

    }
    for (let i = 0; i < categories.length; i++) {
        await axios.get(`https://api.digiseller.ru/api/shop/products?seller_id=549328&category_id=${Object.values(categories[i])}`).then(async res => {
            let items = res.data.product;
            for (let r = 0; r < items.length; r++) {
                arrForIdItems.push(items[r].id)
            }
        })
    }

    await axios.get(`https://api.digiseller.ru/api/products/list?ids=${arrForIdItems}&lang=ru-RU`).then(resIds => {
        let dataItems = []
        if (category === "rec") {
            dataItems = resIds.data.sort(() => Math.random() - 0.5)
        } else if (category === "dis") {
            dataItems = resIds.data.sort((a, b) => a.price_rub - b.price_rub)
        } else {
            dataItems = resIds.data.sort((a, b) => b.cnt_sell - a.cnt_sell)
        }
        console.log(dataItems)
        setDataState(dataItems);
    })

}

export const requestItemsSlider = () => async (dispatch) => {
    let idsString = '';
    let arrSliderItems = [];
    let slides = initialState.slides;
    let arrMainSlidesItems = []

        for (let i = 0; i < slides.length; i++) {
            for (let p = 0; p < 5; p++) {
                idsString += slides[i][p] + ",";
            }}

    await axios.get(`https://api.digiseller.ru/api/products/list?ids=${slides}&lang=ru-RU`).then(response => {
        let items = response.data;
        let tempCountItemsOnSlide = 0;

        for (let q = 0; q < items.length; q++) {
            arrSliderItems.push({
                id: items[q].id,
                cnt_sell: items[q].cnt_sell,
                price_rub: items[q].price_rub,
                imgItem: `https://graph.digiseller.ru/img.ashx?id_d=${items[q].id}&h=470`
            })
            tempCountItemsOnSlide++
            if (tempCountItemsOnSlide === 5) {
                tempCountItemsOnSlide = 0
                arrMainSlidesItems.push(arrSliderItems)
                arrSliderItems = []
            }
        }
    })
    dispatch(addArrayToSlides(arrMainSlidesItems))
    dispatch(initSliderAC(true))
}

export default ProductsReducer;