import './App.css';
import {Routes, Route} from "react-router-dom";
import HeaderContainer from "./componetns/header/headerContainer";
import MainContainer from "./componetns/main/mainContainer";
import {requestItemsFromCategories} from "./redux/productsReducer";
import {compose} from "redux";
import {connect} from "react-redux";
import Item from "./componetns/item/itemContainer";
import {requestLastPurchases} from "./redux/lastPurchasesReducer";
import PreloaderComponent from "./componetns/preloader/preloader";
import Footer from "./componetns/main/footer/footer";
import Catalog from "./componetns/category/CatalogContainer";


const App = (props) => {

    if (!props.initLastPurchases) {
        props.requestLastPurchases();
    }

  return (
      props.initLastPurchases ? <div>
        <HeaderContainer />

        <Routes>
          <Route exact path='/' element={<MainContainer />} />
          <Route path='/product/:id' element={<Item/>} />
          <Route path='/catalog/' element={<Catalog/>} />
            <Route path='/catalog/:categories' element={<Catalog/>} />
        </Routes>

        <Footer />
      </div> : <PreloaderComponent w={300} h={300} main={true}/>
  );
}

let mapStateToProps = (state) => {
    return {
        initLastPurchases: state.LastPurchasesPage.initLastPurchases
    }
}

export default compose(connect(mapStateToProps, {requestItemsFromCategories, requestLastPurchases})) (App);

