import React from "react";
import style from "./itemBlock.module.css";
import {NavLink} from "react-router-dom";
import {IoCart} from "react-icons/io5";

const ItemBlock = ({item}) => {
  return (
      <div className={style.itemLi}>
              <NavLink className={style.navLinkStyle} to={`/product/${item.id}`}>
                  <img className={style.imgItemStyle} src={item.imgItem}
                  />
                  <div className={style.panelItem}>
                      {item.cnt_sell &&
                      <><span className={style.countSellImg}><IoCart/></span>
                          <span className={style.countSell}>{item.cnt_sell}</span></>}
                      <span className={style.priceItem}>{item.price} ₽</span>
                  </div>
              </NavLink>
          </div>
  )
}

export default ItemBlock;