import * as React from "react";
import style from './catalog.module.css';
import MultiRangeSlider from "./dualRange/dualRange";
import {useParams, useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";
import ItemBlock from "../main/ItemBlock/itemBlock";
import Paginator from "../paginator/paginator";
import PreloaderComponent from "../preloader/preloader";
import {Checkbox} from "pretty-checkbox-react";

const Catalog = (props) => {
    const [catalogData, setCatalogData] = useState(props.catalogData);
    const [params, setParams] = useState([])
    const navigate = useNavigate();
    const {categories} = useParams()
    const [minPrice, setMinPrice] = useState(0)
    const [maxPrice, setMaxPrice] = useState(0)
    const [minPriceChanging, setMinPriceChanging] = useState(props.minChangingPriceCatalog)
    const [maxPriceChanging, setMaxPriceChanging] = useState(props.maxChangingPriceCatalog)
    const [catalogChanging, setCatalogChanging] = useState(props.catalogData)
    const [initCatalog, setInitCatalog] = useState(props.initCatalog)
    const [sortFilter, setSortFilter] = useState("lead")

    useEffect(() => {
        setInitCatalog(props.initCatalog)
    }, [props.initCatalog])

    useEffect(() => {
        setCatalogData(props.catalogData)
    }, [props.catalogData])

    useEffect(() => {
        setMinPriceChanging(props.minChangingPriceCatalog)
        setMaxPriceChanging(props.maxChangingPriceCatalog)
    }, [props.minChangingPriceCatalog, props.maxChangingPriceCatalog])

    useEffect(() => {
        let tempItemsLocal2 = []
        if ("lead" === sortFilter) {
            tempItemsLocal2 = catalogData.sort((a, b) => b.cnt_sell - a.cnt_sell)
        } else if ("cheap" === sortFilter) {
            tempItemsLocal2 = catalogData.sort((a, b) => a.price - b.price)
        } else if ("expensive" === sortFilter) {
            tempItemsLocal2 = catalogData.sort((a, b) => b.price - a.price)
        }

        setCatalogChanging(tempItemsLocal2.filter(item => item.price >= minPriceChanging && item.price <= maxPriceChanging))
    }, [minPriceChanging, maxPriceChanging])

    useEffect(() => {
        let tempArray = []
        if (!!categories) {
            categories.split(',').map(category => tempArray.push(category))
            setParams(tempArray)
        } else {
            setParams([])
        }
        props.requestItemsFromCategories(false, (tempArray.length ? tempArray : false))
    }, [categories])

    useEffect(() => {

        let tempMinPrice = 0
        let tempMaxPrice = 0

        for (let i = 0; i < catalogData.length; i++) {
            let currentPrice = catalogData[i].price;
            if (currentPrice > tempMaxPrice) tempMaxPrice = currentPrice
            if (currentPrice < tempMinPrice) tempMinPrice = currentPrice
        }
        setMinPrice(tempMinPrice)
        setMaxPrice(tempMaxPrice)
        setMinPriceChanging(tempMinPrice)
        setMaxPriceChanging(tempMaxPrice)
        props.changeMinAndMaxCatalog(tempMinPrice, tempMaxPrice)
    }, [catalogData])

    let selectSortItems = (e) => {
        setSortFilter(e.target.value)
        // let localCatalogItems = props.catalogData.filter(item => item.price > minPrice && item.price < maxPrice)
        let dataItemsLocal = []
        if ("lead" === e.target.value) {
            dataItemsLocal = catalogChanging.sort((a, b) => b.cnt_sell - a.cnt_sell)
        } else if ("cheap" === e.target.value) {
            dataItemsLocal = catalogChanging.sort((a, b) => a.price - b.price)
        } else if ("expensive" === e.target.value) {
            dataItemsLocal = catalogChanging.sort((a, b) => b.price - a.price)
        }
        setCatalogChanging(dataItemsLocal)
    }

    function Items({currentItems}) {
        return (
            <ul className={style.wrapItems}>
                {currentItems && currentItems.map(item =>
                        <ItemBlock key={item.id} item={item} />
                )}
            </ul>
        );
    }

    const InputCheckBox = ({title}) => {
        const toggleParam = (title) => {
            props.setInitCatalog(false)
            let param = title.toLowerCase()
            let tempArray = params.slice(0)
            if (tempArray.includes(param)) {
                tempArray.splice(tempArray.indexOf(param), 1)
            } else {
                tempArray.push(param)
            }
            setParams(tempArray)
            navigate(`/catalog/${tempArray.join(',')}`)
        }

      return <Checkbox readOnly className={style.checkBoxStyle} checked={!!params.includes(title.toLowerCase())} onClick={() => {
              toggleParam(title)
          }}><span className={style.checkBoxTextStyle}>{title}</span></Checkbox>
    }

    const resetParams = () => {
        props.setInitCatalog(false)
        setParams([])
        navigate('/catalog/')
    }

    return <div className="container-lg center">
                <div className={style.wrapCatalog}>
            <div className={style.wrapFilter}>
                <div>
                    <span className={style.textFilterCatalog}>Цена</span>
                    <br/>
                    <span className={style.textFilterCatalog}>От</span><input onChange={(e) => {
                    props.changeMinAndMaxCatalog(e.target.value, maxPrice)
                }} type="text" className={style.inputPrice} value={minPriceChanging}/>
                    <span className={style.textFilterCatalog}>От</span><input onChange={(e) => {
                    props.changeMinAndMaxCatalog(minPriceChanging, e.target.value)
                }} type="text" className={style.inputPrice} value={maxPriceChanging}/>
                    <MultiRangeSlider
                        catalogData={catalogData}
                        min={minPrice}
                        max={maxPrice}
                        onChange={({min, max}) => {
                            props.changeMinAndMaxCatalog(min,max)
                        }}
                    />
                    <br/>
                    <button onClick={resetParams} type="button" className="btn btn-warning">Сбросить</button>
                    <br/>
                    <br/>
                        <InputCheckBox title={"Minecraft"}/>
                        <InputCheckBox title={"Steam"}/>
                        <InputCheckBox title={"Origin"}/>
                        <InputCheckBox title={"Uplay"}/>
                        <InputCheckBox title={"Xbox"}/>
                        <InputCheckBox title={"Software"}/>

                </div>
            </div>
            <div className={style.wrapItemsCatalog}>
                <div>
                    {initCatalog === false ?
                        <PreloaderComponent w={160} h={160}/> :
                        <>
                            <div className={style.sortWrapCatalog}>
                            <div className="input-group mb-1">
                                <label className="input-group-text" htmlFor="inputGroupSelect01">Сортировать по</label>
                                <select onChange={selectSortItems} className="form-select" id="inputGroupSelect01">
                                    <option value={"lead"}>Лидерам продаж</option>
                                    <option value={"cheap"}>Сначала дешевые</option>
                                    <option value={"expensive"}>Сначала дорогие</option>
                                </select>
                            </div>
                            </div>
                            <Paginator
                                items={catalogChanging}
                                itemsPerPage={15}
                                Items={Items}
                                minPriceChanging={minPriceChanging}
                                maxPriceChanging={maxPriceChanging}/>
                        </>
                        }
                </div>


            </div>
        </div>
    </div>
}

export default Catalog;