import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import Catalog from "./Catalog";
import {
    changeMinAndMaxCatalog,
    requestItemsFromCategories,
    setInitCatalog
} from "../../redux/productsReducer";


let mapStateToProps = (state) => {
    return {
        catalogData: state.SlidesPage.catalogData,
        minChangingPriceCatalog: state.SlidesPage.minChangingPriceCatalog,
        maxChangingPriceCatalog: state.SlidesPage.maxChangingPriceCatalog,
        initCatalog: state.SlidesPage.initCatalog
    }
}

export default compose(connect(mapStateToProps, {requestItemsFromCategories, changeMinAndMaxCatalog, setInitCatalog})) (Catalog);